import React from 'react';
import Header from './components/Header/Header.js'
import News from './components/News/News.js';
import { Switch, Route } from 'react-router-dom'
function App() {
  return (
    <div>
      <Switch>
        <Route path="/" component={News} />
      </Switch>
    </div>
  );
}

export default App;
